import nanoid from 'nanoid';

/**
 * Generates an element specific random ID
 * @param  {String} [name] Name to add in generated ID
 * @return {String}        Generated id
 */
export const edsRandomId = name => {
    if (name) return `eds-${`${name}-`}${nanoid(5)}`;
    return `eds-${nanoid(5)}`;
};
